import { ElMessage } from 'element-plus'
import { BASE_URL, TIME_OUT } from './config'

import DFAxios from './dfAxios'

export const dfRequest = new DFAxios({
  baseURL: BASE_URL,
  timeout: TIME_OUT,
  interceptors: {
    responseInterceptor(res) {
      if (res.status != 'success') {
        ElMessage({
          message: res.msg,
          type: 'error'
        })
        throw res.msg
      }
      return res
    },
    requestInterceptor(config) {
      // console.log('')
      return config
    }
  }
})
